@import "/src/_reset.scss";

$red: #ce2828;
.red {
  color: $red;
}

@mixin myAlert {
  background: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  margin: auto;
  width: 100%;
  max-width: 500px;
}
.my-alert {
  @include myAlert();
}
.my-alert2 {
  @extend .my-alert;
  background: #ffe591;
}
.my-alert p {
  margin-bottom: 0;
}

// 함수 만드는 문법 -> extend 와 비슷하게 쓰인다

